




























































































import { Component, Vue } from 'vue-property-decorator';
import { Form } from 'element-ui';
import { cloneDeep } from 'lodash';

import {
  updateStudent,
  defaultStudentData,
  getCampaignStudents,
} from '../../api/students';

import { IStudentEntity, INTERNSHIP_MODE } from '../../declarations';

import { exportJson2Excel } from '../../utils/excel';
import { formatJson } from '../../utils';

import Pagination from '../../components/Pagination/index.vue';
import CrudBtn from '../../components/CrudBtn/index.vue';

@Component({
  name: 'StudentCampaignsList',
  components: {
    Pagination,
    CrudBtn,
  },
})
export default class extends Vue {
  private tableKey = 0;
  private list: IStudentEntity[] = [];
  private total = 0;

  private listLoading = true;
  private listQuery = {
    page: 1,
    limit: 10,
    name: undefined,
  };

  private downloadLoading = false;

  public created() {
    this.getList();
  }

  private get id() {
    return Number(this.$route.params.id);
  }

  private getList() {
    this.listLoading = true;
    getCampaignStudents(this.id, this.listQuery).then(res => {
      this.list = res ? res.data : [];
      this.total = res ? res.max : 0;
      this.listLoading = false;
    });
  }

  private handleFilter() {
    this.getList();
  }

  private handleDownload() {
    this.downloadLoading = true;
    const tHeader = [
      this.$t('export.id') as string,
      this.$t('export.firstName') as string,
      this.$t('export.lastName') as string,
      this.$t('export.email') as string,
    ];
    const filterVal = ['id', 'firstName', 'lastName', 'email'];
    const data = formatJson(filterVal, this.list);
    exportJson2Excel(tHeader, data, this.$t(
      'export.students.fileName'
    ) as string);
    this.downloadLoading = false;
  }
}
